import React from "react";

const TableHead = ({ name }) => {
  const headerClass1 =
    "text-uppercase text-secondary text-sm font-weight-bolder opacity-7 ";
  const headerClass2 =
    "text-center text-uppercase text-secondary text-sm font-weight-bolder opacity-7 ";
  const headerClass3 =
    "ps-2 text-uppercase text-secondary text-sm font-weight-bolder opacity-7 ";
  const headerClass4 =
    "text-uppercase text-secondary text-sm font-weight-bolder opacity-7 ps-2 ";

  if (name === "motherboard") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass4}>Chipset</th>
          <th className={headerClass2}>Socket</th>
          <th className={headerClass2}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "processor") {
    return (
      <thead>
        <tr>
          <th className={headerClass3}>Name</th>
          <th className={headerClass4}>Vendor</th>
          <th className={headerClass1}>Type</th>
          <th className={headerClass3}>Socket</th>
          <th className={headerClass3}>Gen</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "memory") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass4}>Type</th>
          <th className={headerClass3}>Size</th>
          <th className={headerClass3}>Speed</th>
          <th className={headerClass3}>Channel</th>
          <th className={headerClass3}>price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "storage") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass4}>Type</th>
          <th className={headerClass3}>Slot Type</th>
          <th className={headerClass3}>Size</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "cassing") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Form Factor</th>
          <th className={headerClass3}>Front Panel</th>
          <th className={headerClass3}>price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "vga") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Memory</th>
          <th className={headerClass3}>Req PSU</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "user") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass4}>Email</th>
          <th className={headerClass3}>Role</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "psu") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Watt</th>
          <th className={headerClass3}>Sertification</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "monitor") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Panel</th>
          <th className={headerClass3}>Resolusi</th>
          <th className={headerClass3}>Port</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "keyboard") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Type</th>
          <th className={headerClass3}>Conection</th>
          <th className={headerClass3}>Size</th>
          <th className={headerClass3}>Switch Type</th>
          <th className={headerClass3}>RGB</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "mouse") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Conection</th>
          <th className={headerClass3}>Button</th>
          <th className={headerClass3}>RGB</th>
          <th className={headerClass3}>Resolusi</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "headset") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Conection</th>
          <th className={headerClass3}>Cable Lenght</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "speaker") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Conection</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "gamingChair") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "gamingDesk") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Size</th>
          <th className={headerClass3}>Price</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  } else if (name === "mousepad") {
    return (
      <thead>
        <tr>
          <th className={headerClass1}>Name</th>
          <th className={headerClass3}>Size</th>
          <th className={headerClass3}>Ketebalan</th>
          <th className={headerClass2}>Action</th>
        </tr>
      </thead>
    );
  }
};

export default TableHead;
