import React from "react";
import NavbarCollapse from "./sidenav/NavbarCollapse";
import SidenavHeader from "./sidenav/SidenavHeader";

const SideNav = () => {
  return (
    <aside
      className="sidenav navbar navbar-vertical navbar-expand-xs border-0 border-radius-xl my-3 fixed-start ms-3 sidenav1 bg-gradient-secondary"
      id="sidenav-main"
    >
      <SidenavHeader />
      <hr className="horizontal dark mt-0" />
      <NavbarCollapse />
    </aside>
  );
};

export default SideNav;
