import React, { useEffect } from "react";
import NavbarItem from "./NavbarItem";
import { useDispatch, useSelector } from "react-redux";
import { getMe } from "../../features/authSlice";
import Accordion from "react-bootstrap/Accordion";

const NavbarCollapse = () => {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);

  useEffect(() => {
    dispatch(getMe());
  }, [dispatch]);
  const sidebar = [
    {
      nama: "Motherboard",
      icon: "fa-solid fa-microchip",
      link: "/motherboard",
    },
    {
      nama: "Processor",
      icon: "fa-solid fa-microchip",
      link: "/processor",
    },
    {
      nama: "Memory",
      icon: "fa-solid fa-memory",
      link: "/memory",
    },
    {
      nama: "Storage",
      icon: "fa-solid fa-hard-drive",
      link: "/storage",
    },
    {
      nama: "Cassing",
      icon: "fa-solid fa-mobile-screen",
      link: "/cassing",
    },
  ];
  return (
    <div
      className=" d-block collapse w-auto  max-height-vh-100"
      id="sidenav-collapse-main"
    >
      <ul className="navbar-nav">
        <Accordion defaultActiveKey="0" className=" mx-3" flush>
          <Accordion.Item eventKey="0">
            <Accordion.Header className="border rounded-2 mt-3 mb-2">
              Home
            </Accordion.Header>
            <Accordion.Body>
              <NavbarItem name="Dashboard" icon="fa-solid fa-house" link="/" />
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="1">
            <Accordion.Header className="border rounded-2 mt-3 mb-2">
              Compatible Component
            </Accordion.Header>
            <Accordion.Body>
              {sidebar.map((data, index) => (
                <NavbarItem
                  name={data.nama}
                  icon={data.icon}
                  link={data.link}
                  key={index}
                />
              ))}
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="2">
            <Accordion.Header className="border rounded-2 mt-3 mb-2">
              Optional Component
            </Accordion.Header>
            <Accordion.Body>
              <NavbarItem
                name="Graphic Card"
                icon="fa-solid fa-microchip"
                link="/graphic-card"
              />
              <NavbarItem name="PSU" icon="fa-solid fa-power-off" link="/psu" />
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="3">
            <Accordion.Header className="border rounded-2 mt-3 mb-2">
              More Component
            </Accordion.Header>
            <Accordion.Body>
              <NavbarItem
                name="Monitor"
                icon="fa-solid fa-desktop"
                link="/monitor"
              />
              <NavbarItem
                name="Keyboard"
                icon="fa-solid fa-keyboard"
                link="/keyboard"
              />
              <NavbarItem name="Mouse" icon="fa-solid fa-mouse" link="/mouse" />
              <NavbarItem
                name="Headset"
                icon="fa-solid fa-headphones"
                link="/headset"
              />
              <NavbarItem
                name="Speaker"
                icon="fa-solid fa-volume-high"
                link="/speaker"
              />
              <NavbarItem
                name="Gaming Chair"
                icon="fa-solid fa-chair"
                link="/gaming-chair"
              />
              <NavbarItem
                name="Gaming Desk"
                icon="fa-solid fa-house"
                link="/gaming-desk"
              />
              <NavbarItem
                name="Mousepad"
                icon="fa-solid fa-rectangle-xmark"
                link="/mousepad"
              />
            </Accordion.Body>
          </Accordion.Item>

          {user && user.user.role === "admin" && (
            <>
              <Accordion.Item eventKey="4">
                <Accordion.Header className="border rounded-2 mt-3 mb-2">
                  Users
                </Accordion.Header>
                <Accordion.Body>
                  <NavbarItem
                    name="Users List"
                    icon="fa-solid fa-house"
                    link="/users"
                  />
                </Accordion.Body>
              </Accordion.Item>
            </>
          )}
        </Accordion>
      </ul>
    </div>
  );
};

export default NavbarCollapse;
