import React from "react";
import TableHead from "./TableHead";
import { Button } from "react-bootstrap";
import { useState, useEffect, useReducer } from "react";
import axios from "axios";
import TableBody from "./TableBody";
import ModalAdd from "../modal/ModalAdd";

const TableContent = ({ name }) => {
  const [data, setData] = useState([]);
  const [show, setShow] = useState(false);
  const [reducerValue, forceUpdate] = useReducer((x) => x + 1, 0);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const url = process.env.REACT_APP_API_URL;

  useEffect(() => {
    const getDatas = async () => {
      const response = await axios.get(`${url}/api/v1/${name}`);
      setData(response.data);
    };
    getDatas();
  }, [reducerValue]);

  return (
    <>
      <div className="container-xxl card mb-4 mt-4">
        <div className="card-header pb-0">
          <h4 className="text-primary fw-bolder text-capitalize">
            {name} List
          </h4>
          <Button onClick={handleShow} className=" float-end">
            Add
          </Button>
        </div>
        <div className="card-body px-0 pt-0 pb-2">
          <div className="table-responsive p-0">
            <table className="table align-items-center mb-0">
              <TableHead name={name} />
              <TableBody name={name} datas={data} forceUpdate={forceUpdate} />
            </table>
          </div>
        </div>
      </div>
      <ModalAdd
        name={name}
        handleClose={handleClose}
        show={show}
        forceUpdate={forceUpdate}
      />
    </>
  );
};

export default TableContent;
