import React from "react";

const PageNotFound = () => {
  return (
    <div
      className=" d-flex justify-content-center align-items-center"
      style={{ height: "100vh" }}
    >
      <div className=" display-1 text-secondary fw-bold">404</div>
      <div className=" display-5 text-danger fw-bold">Page Not Found</div>
    </div>
  );
};

export default PageNotFound;
