import React, { useState, useEffect } from "react";
import { Button, Dropdown, NavDropdown } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { LogOut, reset } from "../../../features/authSlice";
import { getMe } from "../../../features/authSlice";

const NavbarMain = ({ name, handleShow }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.auth);
  const [username, setUserNama] = useState("");
  const [email, setEmail] = useState("");
  const [role, setRole] = useState("");

  useEffect(() => {
    dispatch(getMe());
  }, [dispatch]);

  useEffect(() => {
    if (user !== null) {
      setUserNama(user.user.name);
      setEmail(user.user.email);
      setRole(user.user.role);
    }
  });

  const logout = () => {
    dispatch(LogOut());
    dispatch(reset());
    navigate("/login");
  };

  return (
    <nav
      className="navbar navbar-main navbar-expand px-0 mx-4 shadow-none border-radius-xl"
      id="navbarBlur"
      navbar-scroll="true"
    >
      <div className="container-fluid py-1 px-3">
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb bg-transparent mb-0 pb-0 pt-1 px-0 me-sm-6 me-5">
            <li className="breadcrumb-item text-sm">
              <a className="opacity-5 text-dark" href="#">
                Pages
              </a>
            </li>
            <li
              className="breadcrumb-item text-sm text-dark active text-capitalize"
              aria-current="page"
            >
              {name}
            </li>
          </ol>
          <h6 className="font-weight-bolder mb-0 text-capitalize">{name}</h6>
        </nav>
        <div
          className="collapse navbar-collapse mt-sm-0 mt-2 me-md-0 me-sm-4"
          id="navbar"
        >
          <div className="ms-md-auto pe-md-3 d-flex align-items-center"></div>
          <ul className="navbar-nav  justify-content-end">
            <li className="nav-item dropdown pe-2 d-flex align-items-center">
              <a
                href=""
                className="nav-link text-body p-0 text-capitalize"
                id="dropdownMenuButton"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <i className="fa fa-user me-sm-1"></i>
                {username}
              </a>
              <ul
                className="dropdown-menu  dropdown-menu-end px-2 py-3 me-sm-n4"
                aria-labelledby="dropdownMenuButton"
              >
                <li className="mb-2">
                  <a className="dropdown-item border-radius-md" href="">
                    <div className="d-flex py-1">
                      <div className="my-auto">
                        <img
                          src="../assets/img/team-2.jpg"
                          className="avatar avatar-sm  me-3 "
                        />
                      </div>
                      <div className="d-flex flex-column justify-content-center">
                        <h6 className="text-sm font-weight-normal mb-1">
                          <span className="font-weight-bold">{email}</span>
                        </h6>
                        <p className="text-xs text-secondary mb-0 text-capitalize">
                          {role}
                        </p>
                      </div>
                    </div>
                  </a>
                </li>
                <li>
                  <Button onClick={logout} className="ms-3 bg-danger btn-sm">
                    Log Out
                  </Button>
                </li>
              </ul>
            </li>

            <li className="nav-item d-xl-none ps-3 d-flex align-items-center">
              <a
                onClick={handleShow}
                className="nav-link text-body p-0 me-2 float-end hidden"
                id="iconNavbarSidenav"
              >
                <div className="sidenav-toggler-inner cursor-pointer">
                  <i className="sidenav-toggler-line"></i>
                  <i className="sidenav-toggler-line"></i>
                  <i className="sidenav-toggler-line"></i>
                </div>
              </a>
              <a href="#" className=""></a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default NavbarMain;
