import React from "react";
import { useState } from "react";
import axios from "axios";
import { Modal, Button, Form, Col } from "react-bootstrap";
import Error from "../error/Error";

const ModalAdd = ({ name, handleClose, show, forceUpdate }) => {
  const [error, setError] = useState(false);
  const [title, setTitle] = useState("");
  const [vendor, setVendor] = useState("");
  const [chipset, setChipset] = useState("");
  const [socket, setSocket] = useState("");
  const [gen, setGen] = useState("");
  const [memory, setMemory] = useState("");
  const [memorySlot, setMemorySlot] = useState("");
  const [sataPort, setSataPort] = useState("");
  const [m2Slot, setM2Slot] = useState("");
  const [usbPort, setUsbPort] = useState("");
  const [display, setDisplay] = useState("");
  const [formFactor, setFormFactor] = useState("");
  const [lanPort, setLanPort] = useState("");
  const [price, setPrice] = useState("");
  const [file, setFile] = useState("");
  const [type, setType] = useState("");
  const [frontPanel, setFrontPanel] = useState("");
  const [size, setSize] = useState("");
  const [speed, setSpeed] = useState("");
  const [channel, setChannel] = useState("");
  const [slotType, setSlotType] = useState("");
  const [gpu, setGpu] = useState("");
  const [reqPsu, setReqPsu] = useState("");
  const [watt, setWatt] = useState("");
  const [sertification, setSertification] = useState("");
  const [panel, setPanel] = useState("");
  const [resolusi, setResolusi] = useState("");
  const [port, setPort] = useState("");
  const [connection, setConnection] = useState("");
  const [numberKeys, setNumberKeys] = useState("");
  const [switchType, setSwitchType] = useState("");
  const [rgb, setRgb] = useState("");
  const [button, setButton] = useState("");
  const [cableLenght, setCableLenght] = useState("");
  const [ketebalan, setKetebalan] = useState("");

  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confPassword, setConfPassword] = useState("");
  const [role, setRole] = useState("");

  const [listType, setListType] = useState([
    {
      type: "Select Vendor First...",
    },
  ]);
  const [isChecked, setIsChecked] = useState(false);
  const [inputType, setInputType] = useState("password");

  const [harga, setHarga] = useState("");
  const url = process.env.REACT_APP_API_URL;

  const getHarga = (e) => {
    console.log(e.taget.value);
  };

  const getTypeProcessor = async (vendor) => {
    try {
      const response = await axios.get(
        `${url}/api/v1/processor/vendor/${vendor}`
      );
      setListType(response.data);
    } catch (error) {
      console.log(error);
    }
  };

  const loadImage = (e) => {
    const image = e.target.files[0];
    setFile(image);
  };

  const resetForm = () => {
    setTitle("");
    setFrontPanel("");
    setVendor("");
    setChipset("");
    setSocket("");
    setGen("");
    setMemory("");
    setMemorySlot("");
    setSataPort("");
    setM2Slot("");
    setUsbPort("");
    setDisplay("");
    setFormFactor("");
    setLanPort("");
    setPrice("");
    setFile("");
    setType("");
    setSize("");
    setSpeed("");
    setChannel("");
    setSlotType("");
    setEmail("");
    setUsername("");
    setPassword("");
    setConfPassword("");
    setRole("");
    setInputType("password");
    setGpu("");
    setReqPsu("");
    setWatt("");
    setSertification("");
    setPanel("");
    setResolusi("");
    setPort("");
    setConnection("");
    setNumberKeys("");
    setSwitchType("");
    setRgb("");
    setButton("");
    setCableLenght("");
    setKetebalan("");
  };

  const closeError = () => {
    setError(false);
  };

  const saveProduct = async (e) => {
    e.preventDefault();
    const formData = new FormData();

    formData.append("title", title.toUpperCase());
    formData.append("vendor", vendor.toUpperCase());
    formData.append("chipset", chipset.toUpperCase());
    formData.append("socket", socket.toUpperCase());
    formData.append("memory", memory.toUpperCase());
    formData.append("memorySlot", memorySlot.toUpperCase());
    formData.append("sataPort", sataPort.toUpperCase());
    formData.append("m2Slot", m2Slot.toUpperCase());
    formData.append("usbPort", usbPort.toUpperCase());
    formData.append("display", display.toUpperCase());
    formData.append("formFactor", formFactor.toUpperCase());
    formData.append("lanPort", lanPort.toUpperCase());
    formData.append("type", type.toUpperCase());
    formData.append("gen", gen.toLowerCase());
    formData.append("size", size.toUpperCase());
    formData.append("speed", speed.toUpperCase());
    formData.append("channel", channel.toUpperCase());
    formData.append("slotType", slotType.toUpperCase());
    formData.append("name", title.toUpperCase());
    formData.append("frontPanel", frontPanel.toUpperCase());
    formData.append("gpu", gpu.toUpperCase());
    formData.append("reqPsu", reqPsu.toUpperCase());
    formData.append("watt", watt.toUpperCase());
    formData.append("sertification", sertification.toUpperCase());
    formData.append("panel", panel.toUpperCase());
    formData.append("resolusi", resolusi.toUpperCase());
    formData.append("port", port.toUpperCase());
    formData.append("connection", connection.toUpperCase());
    formData.append("numberKeys", numberKeys.toUpperCase());
    formData.append("switchType", switchType.toUpperCase());
    formData.append("rgb", rgb.toUpperCase());
    formData.append("button", button.toUpperCase());
    formData.append("cableLenght", cableLenght.toUpperCase());
    formData.append("ketebalan", ketebalan.toUpperCase());
    formData.append("price", price);
    formData.append("file", file);
    formData.append("username", username);
    formData.append("email", email);
    formData.append("password", password);
    formData.append("confPassword", confPassword);
    formData.append("role", role);

    try {
      await axios.post(`${url}/api/v1/${name}`, formData, {
        headers: {
          "content-type": "multipart/form-data",
        },
      });
    } catch (error) {
      const errorStatus = error;
      console.log(errorStatus);
      setError(true);
    }
    handleClose();
    resetForm();
    forceUpdate();
  };

  if (error === true) {
    return (
      <Modal show={error} onHide={closeError} backdrop="static">
        <Modal.Header>
          <Modal.Title>Adding Error</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Error code="422" />
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="danger"
            type="reset"
            className=" float-end"
            onClick={closeError}
          >
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }

  if (name === "motherboard") {
    return (
      <>
        <Modal show={show} size="lg" onHide={handleClose} backdrop="static">
          <Form onSubmit={saveProduct} onReset={resetForm}>
            <Modal.Header>
              <Modal.Title>Add New Motherboard</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className="card-body row">
                <Col lg="6" xs="12" md="6">
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      autoFocus
                      value={title}
                      onChange={(e) => setTitle(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Vendor</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={vendor}
                      onChange={(e) => setVendor(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Chipset</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={chipset}
                      onChange={(e) => setChipset(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Memori Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setMemory(e.target.value);
                      }}
                    >
                      <option>--Select Type--</option>
                      <option value="ddr 2">DDR 2</option>
                      <option value="ddr 3">DDR 3</option>
                      <option value="ddr 4">DDR 4</option>
                      <option value="ddr 5">DDR 5</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Jumlah Memori Slot</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={memorySlot}
                      onChange={(e) => setMemorySlot(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Port Sata</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={sataPort}
                      onChange={(e) => setSataPort(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Slot M.2</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={m2Slot}
                      onChange={(e) => setM2Slot(e.target.value)}
                    />
                  </Form.Group>
                </Col>
                <Col lg="6" xs="12" md="6">
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>USB Port</Form.Label>
                    <Form.Control
                      as="textarea"
                      value={usbPort}
                      onChange={(e) => setUsbPort(e.target.value)}
                      placeholder=""
                    />
                  </Form.Group>

                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Display</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={display}
                      onChange={(e) => setDisplay(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formFactor">
                    <Form.Label>Form Factor</Form.Label> <br />
                    <Form.Check
                      inline
                      label="atx"
                      name="choseFormFactor"
                      type="radio"
                      id="atx"
                      value="id"
                      required
                      onClick={(e) => setFormFactor(e.target.id)}
                    />
                    <Form.Check
                      inline
                      label="micro atx"
                      name="choseFormFactor"
                      type="radio"
                      id="matx"
                      onClick={(e) => setFormFactor(e.target.id)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Port Lan</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={lanPort}
                      onChange={(e) => setLanPort(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Socket</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={socket}
                      onChange={(e) => setSocket(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Price</Form.Label>
                    <Form.Control
                      type="number"
                      required
                      value={price}
                      onChange={(e) => setPrice(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-1" controlId="formBasicEmail">
                    <Form.Label>Image</Form.Label>
                    <Form.Control type="file" onChange={loadImage} required />
                  </Form.Group>
                </Col>
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button
                variant="danger"
                type="reset"
                className=" float-end"
                onClick={handleClose}
              >
                Cancel
              </Button>
              <Button
                variant="secondary"
                type="submit"
                className=" float-end ms-2"
              >
                Save
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      </>
    );
  } else if (name === "user") {
    const showPassword = () => {
      if (inputType === "password") {
        setInputType("text");
      } else {
        setInputType("password");
      }
    };
    return (
      <>
        <Modal show={show} onHide={handleClose} backdrop="static">
          <Form onSubmit={saveProduct} onReset={resetForm}>
            <Modal.Header>
              <Modal.Title>Add New Account</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className="card-body">
                <Form.Group className="mb-3">
                  <Form.Label>Name</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    autoFocus
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    type={inputType}
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label>Confirm Password</Form.Label>
                  <Form.Control
                    type={inputType}
                    required
                    value={confPassword}
                    onChange={(e) => setConfPassword(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicCheckbox">
                  <Form.Check
                    type="checkbox"
                    label="Show Password"
                    onChange={showPassword}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label>Role</Form.Label>
                  <Form.Select
                    aria-label="select role"
                    onChange={(e) => setRole(e.target.value)}
                  >
                    <option>--Select Role--</option>
                    <option value="admin">Admin</option>
                    <option value="manager">Manager</option>
                    <option value="employe">Employe</option>
                  </Form.Select>
                </Form.Group>
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button
                variant="danger"
                type="reset"
                className=" float-end"
                onClick={handleClose}
              >
                Cancel
              </Button>
              <Button
                variant="secondary"
                type="submit"
                className=" float-end ms-2"
              >
                Save
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      </>
    );
  }

  return (
    <>
      <Modal show={show} onHide={handleClose} backdrop="static">
        <Form onSubmit={saveProduct} onReset={resetForm}>
          <Modal.Header>
            <Modal.Title>Add New </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="card-body">
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Produk Name / Brand"
                  required
                  autoFocus
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                />
              </Form.Group>
              {name !== "processor" &&
              name !== "memory" &&
              name !== "storage" ? (
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Vendor</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    value={vendor}
                    onChange={(e) => setVendor(e.target.value)}
                  />
                </Form.Group>
              ) : (
                ""
              )}
              {name === "processor" ? (
                <>
                  {" "}
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Vendor</Form.Label>
                    <Form.Select
                      aria-label="select role"
                      onChange={(e) => {
                        setVendor(e.target.value);
                        getTypeProcessor(e.target.value);
                      }}
                    >
                      <option>--Select Vendor--</option>
                      <option value="Intel">Intel</option>
                      <option value="AMD">AMD</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setType(e.target.value);
                      }}
                    >
                      <option>--Select Type--</option>
                      {listType
                        ? listType.map((type, index) => {
                            return (
                              <option key={index} value={type.type}>
                                {type.type}
                              </option>
                            );
                          })
                        : () => {
                            return <option value="AMD">AMD</option>;
                          }}
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Socket</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={socket}
                      onChange={(e) => setSocket(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Gen</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={gen}
                      onChange={(e) => setGen(e.target.value)}
                    />
                  </Form.Group>
                </>
              ) : (
                ""
              )}

              {name === "memory" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setType(e.target.value);
                      }}
                    >
                      <option>--Select Type--</option>
                      <option value="ddr 2">DDR 2</option>
                      <option value="ddr 3">DDR 3</option>
                      <option value="ddr 4">DDR 4</option>
                      <option value="ddr 5">DDR 5</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Channel</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setChannel(e.target.value);
                      }}
                    >
                      <option>--Select --</option>
                      <option value="single channel">Single Channel</option>
                      <option value="dual channel">Dual Channel</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Speed</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={speed}
                      onChange={(e) => setSpeed(e.target.value)}
                    />
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "memory" ||
              name === "storage" ||
              name === "gamingDesk" ||
              name === "mousepad" ||
              name === "vga" ? (
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Size</Form.Label>
                  {name === "memory" || name === "vga" ? (
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setSize(e.target.value);
                      }}
                    >
                      <option>--Select --</option>
                      <option value="2 gb">2 Gb</option>
                      <option value="4 gb">4 GB</option>
                      <option value="6 gb">6 GB</option>
                      <option value="8 gb">8 GB</option>
                      <option value="16 gb">16 GB</option>
                      <option value="32 gb">32 GB</option>
                    </Form.Select>
                  ) : (
                    <Form.Control
                      type="text"
                      required
                      value={size}
                      placeholder="120 GB"
                      onChange={(e) => setSize(e.target.value)}
                    />
                  )}
                </Form.Group>
              ) : (
                ""
              )}
              {name === "storage" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setType(e.target.value);
                      }}
                    >
                      <option>--Select Type--</option>
                      <option value="hdd">Harddisk</option>
                      <option value="sdd">SSD</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Slot Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setSlotType(e.target.value);
                      }}
                    >
                      <option>--Select Slot Type--</option>
                      <option value="sata">SATA</option>
                      <option value="msata">MSATA</option>
                      <option value="m.2 sata">M.2 SATA</option>
                      <option value="m.2 nvme">M.2 NVME</option>
                    </Form.Select>
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "cassing" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Form Factor</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setFormFactor(e.target.value);
                      }}
                    >
                      <option>--Select Form Factor--</option>
                      <option value="atx">ATX</option>
                      <option value="matx">MATX</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Front Panel</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={frontPanel}
                      onChange={(e) => setFrontPanel(e.target.value)}
                    />
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "vga" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setType(e.target.value);
                      }}
                    >
                      <option>--Select Type--</option>
                      <option value="gddr 4">GDDR 4</option>
                      <option value="gddr 5">GDDR 5</option>
                      <option value="gddr 6">GDDR 6</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Request PSU</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={reqPsu}
                      onChange={(e) => setReqPsu(e.target.value)}
                    />
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "psu" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Watt</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={watt}
                      onChange={(e) => setWatt(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Sertification</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={sertification}
                      onChange={(e) => setSertification(e.target.value)}
                    />
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "monitor" || name === "mouse" ? (
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Resolusi</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    value={resolusi}
                    onChange={(e) => setResolusi(e.target.value)}
                  />
                </Form.Group>
              ) : (
                ""
              )}

              {name === "monitor" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Panel</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={panel}
                      onChange={(e) => setPanel(e.target.value)}
                    />
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Port</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      value={port}
                      onChange={(e) => setPort(e.target.value)}
                    />
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "mouse" ? (
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Button</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    value={button}
                    onChange={(e) => setButton(e.target.value)}
                  />
                </Form.Group>
              ) : (
                ""
              )}
              {name === "keyboard" ||
              name === "mouse" ||
              name === "headset" ||
              name === "speaker" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Connection</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setConnection(e.target.value);
                      }}
                    >
                      <option>-- Select Type Connection --</option>
                      <option value="wired">Wired</option>
                      <option value="wireless">Wireless</option>
                      <option value="bluetooth">Bluetooth</option>
                    </Form.Select>
                  </Form.Group>
                  {name === "keyboard" || name === "mouse" ? (
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                      <Form.Label>RGB</Form.Label>
                      <Form.Select
                        aria-label="Default select example"
                        onChange={(e) => {
                          setRgb(e.target.value);
                        }}
                      >
                        <option>-- Select --</option>
                        <option value="no">NO</option>
                        <option value="yes">YES</option>
                      </Form.Select>
                    </Form.Group>
                  ) : (
                    ""
                  )}
                </>
              ) : (
                ""
              )}
              {name === "keyboard" ? (
                <>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setType(e.target.value);
                      }}
                    >
                      <option>-- Select Type --</option>
                      <option value="standard">Standard</option>
                      <option value="mechanical">Mechanical</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Size</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setSize(e.target.value);
                      }}
                    >
                      <option>-- Select Type --</option>
                      <option value="standard">Full-Size / 100%</option>
                      <option value="80%">80%</option>
                      <option value="75%">75%</option>
                      <option value="60%">60%</option>
                    </Form.Select>
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Switch Type</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setSwitchType(e.target.value);
                      }}
                    >
                      <option>-- Select Type Switch --</option>
                      <option value="none">None</option>
                      <option value="blue switch">Blue Switch</option>
                      <option value="red switch">Red Switch</option>
                      <option value="brown switch">Brown Switch</option>
                    </Form.Select>
                  </Form.Group>
                </>
              ) : (
                ""
              )}
              {name === "headset" ? (
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Cable Lenght</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    value={cableLenght}
                    onChange={(e) => setCableLenght(e.target.value)}
                  />
                </Form.Group>
              ) : (
                ""
              )}
              {name === "mousepad" ? (
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Ketebalan</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    value={ketebalan}
                    onChange={(e) => setKetebalan(e.target.value)}
                  />
                </Form.Group>
              ) : (
                ""
              )}

              <Form.Group className="mb-1" controlId="formBasicEmail">
                <Form.Label>Price</Form.Label>
                <Form.Control
                  type="number"
                  required
                  value={price}
                  placeholder="Rp.0"
                  onChange={(e) => setPrice(e.target.value)}
                />
              </Form.Group>
              {name !== "storage" && name !== "processor" ? (
                <Form.Group className="mb-1" controlId="formBasicEmail">
                  <Form.Label>Image</Form.Label>
                  <Form.Control type="file" onChange={loadImage} required />
                </Form.Group>
              ) : (
                ""
              )}
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="danger"
              type="reset"
              className=" float-end"
              onClick={handleClose}
            >
              Cancel
            </Button>
            <Button
              variant="secondary"
              type="submit"
              className=" float-end ms-2"
            >
              Save
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
};

export default ModalAdd;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
