import NavbarCollapse from "./sidenav/NavbarCollapse";
import SidenavHeader from "./sidenav/SidenavHeader";
import { Offcanvas } from "react-bootstrap";

function OffCanvas({ show, handleClose }) {
  return (
    <Offcanvas
      show={show}
      onHide={handleClose}
      className="bg-gradient-secondary"
    >
      <aside
        className="sidenav navbar navbar-vertical navbar-expand-xs border-0 border-radius-xl my-3 fixed-start "
        id="sidenav-main"
      >
        <Offcanvas.Header>
          <Offcanvas.Title>
            <SidenavHeader />
          </Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <hr className="horizontal dark mt-0" />
          <NavbarCollapse />
        </Offcanvas.Body>
      </aside>
    </Offcanvas>
  );
}

export default OffCanvas;
