import React, { useState } from "react";
import axios from "axios";
import { Modal, Form, Button, Col } from "react-bootstrap";

const ModalUpdate = ({ show, handleClose, datas, name, forceUpdate }) => {
  const data = datas;
  const [newTitle, setNewTitle] = useState("");
  const [newVendor, setNewVendor] = useState("");
  const [newChipset, setNewChipset] = useState("");
  const [newSocket, setNewSocket] = useState("");
  const [newGen, setNewGen] = useState("");
  const [newMemory, setNewMemory] = useState("");
  const [newMemorySlot, setNewMemorySlot] = useState("");
  const [newSataPort, setNewSataPort] = useState("");
  const [newM2Slot, setNewM2Slot] = useState("");
  const [newUsbPort, setNewUsbPort] = useState("");
  const [newDisplay, setNewDisplay] = useState("");
  const [newLanPort, setNewLanPort] = useState("");
  const [newFormFactor, setNewFormFactor] = useState("");
  const [newPrice, setNewPrice] = useState("");
  const [newFile, setNewFile] = useState("");
  const [newType, setNewType] = useState("");
  const [newSlotType, setNewSlotType] = useState("");
  const [newSize, setNewSize] = useState("");
  const [newSpeed, setNewSpeed] = useState("");
  const [newChannel, setNewChannel] = useState("");
  const [newUsername, setNewUsername] = useState("");
  const [newEmail, setNewEmail] = useState("");
  const [newRole, setNewRole] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newConfPassword, setNewConfPassword] = useState("");
  const [newFrontPanel, setNewFrontPanel] = useState("");
  const [newGpu, setNewGpu] = useState("");
  const [newReqPsu, setNewReqPsu] = useState("");
  const [newWatt, setNewWatt] = useState("");
  const [newSertification, setNewSertification] = useState("");
  const [newPanel, setNewPanel] = useState("");
  const [newResolusi, setNewResolusi] = useState("");
  const [newPort, setNewPort] = useState("");
  const [newConnection, setNewConnection] = useState("");
  const [newNumberKeys, setNewNumberKeys] = useState("");
  const [newSwitchType, setNewSwitchType] = useState("");
  const [newRgb, setNewRgb] = useState("");
  const [newButton, setNewButton] = useState("");
  const [newCableLenght, setNewCableLenght] = useState("");
  const [newKetebalan, setNewKetebalan] = useState("");

  const [error, setError] = useState(false);
  const loadImage = (e) => {
    const image = e.target.files[0];
    setNewFile(image);
  };

  const [listType, setListType] = useState([
    {
      type: "Select Vendor First...",
    },
  ]);

  const getTypeProcessor = async (vendor) => {
    try {
      const response = await axios.get(
        `http://localhost:5000/api/v1/processor/vendor/${vendor}`
      );
      setListType(response.data);
    } catch (error) {
      console.log(error);
    }
  };

  const resetForm = () => {
    setNewTitle("");
    setNewFrontPanel("");
    setNewVendor("");
    setNewChipset("");
    setNewSocket("");
    setNewGen("");
    setNewMemory("");
    setNewMemorySlot("");
    setNewSataPort("");
    setNewM2Slot("");
    setNewUsbPort("");
    setNewDisplay("");
    setNewFormFactor("");
    setNewLanPort("");
    setNewPrice("");
    setNewFile("");
    setNewType("");
    setNewSize("");
    setNewSpeed("");
    setNewChannel("");
    setNewSlotType("");
    setNewUsername("");
    setNewEmail("");
    setNewRole("");
    setNewPassword("");
    setNewConfPassword("");
    setNewRole("");
    setNewGpu("");
    setNewReqPsu("");
    setNewWatt("");
    setNewSertification("");
    setNewPanel("");
    setNewResolusi("");
    setNewPort("");
    setNewConnection("");
    setNewNumberKeys("");
    setNewSwitchType("");
    setNewRgb("");
    setNewButton("");
    setNewCableLenght("");
    setNewKetebalan("");
  };
  const url = process.env.REACT_APP_API_URL;

  const updateProduct = async (e) => {
    e.preventDefault();
    const formData = new FormData();

    formData.append("title", newTitle.toUpperCase());
    formData.append("vendor", newVendor.toUpperCase());
    formData.append("chipset", newChipset.toUpperCase());
    formData.append("socket", newSocket.toUpperCase());
    formData.append("memory", newMemory.toUpperCase());
    formData.append("memorySlot", newMemorySlot.toUpperCase());
    formData.append("sataPort", newSataPort.toUpperCase());
    formData.append("m2Slot", newM2Slot.toUpperCase());
    formData.append("usbPort", newUsbPort.toUpperCase());
    formData.append("display", newDisplay.toUpperCase());
    formData.append("formFactor", newFormFactor.toUpperCase());
    formData.append("lanPort", newLanPort.toUpperCase());
    formData.append("file", newFile);
    formData.append("type", newType.toUpperCase());
    formData.append("gen", newGen.toLowerCase());
    formData.append("size", newSize.toUpperCase());
    formData.append("speed", newSpeed.toUpperCase());
    formData.append("channel", newChannel.toUpperCase());
    formData.append("slotType", newSlotType.toUpperCase());
    formData.append("frontPanel", newFrontPanel.toUpperCase());
    formData.append("gpu", newGpu.toUpperCase());
    formData.append("reqPsu", newReqPsu.toUpperCase());
    formData.append("watt", newWatt.toUpperCase());
    formData.append("sertification", newSertification.toUpperCase());
    formData.append("panel", newPanel.toUpperCase());
    formData.append("resolusi", newResolusi.toUpperCase());
    formData.append("port", newPort.toUpperCase());
    formData.append("connection", newConnection.toUpperCase());
    formData.append("numberKeys", newNumberKeys.toUpperCase());
    formData.append("switchType", newSwitchType.toUpperCase());
    formData.append("rgb", newRgb.toUpperCase());
    formData.append("button", newButton.toUpperCase());
    formData.append("cableLenght", newCableLenght.toUpperCase());
    formData.append("ketebalan", newKetebalan.toUpperCase());
    formData.append("username", newUsername);
    formData.append("email", newEmail);
    formData.append("password", newPassword);
    formData.append("confPassword", newConfPassword);
    formData.append("role", newRole);
    formData.append("frontPanel", newFrontPanel.toUpperCase());
    formData.append("price", newPrice);

    try {
      let id;
      if (data.uuid === undefined || data.uuid === null) {
        id = data.id;
      } else {
        id = data.uuid;
      }
      await axios.patch(`${url}/api/v1/${name}/${id}`, formData, {
        headers: {
          "content-type": "multipart/form-data",
        },
      });
    } catch (error) {
      const errorStatus = error;
      console.log(errorStatus);
      setError(true);
    }

    handleClose();
    resetForm();
    forceUpdate();
  };
  if (name === "motherboard") {
    return (
      <Modal size="lg" show={show} onHide={handleClose} backdrop="static">
        <Form onSubmit={updateProduct} onReset={resetForm}>
          <Modal.Header>
            <Modal.Title>Update Motherboard</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="card-body row">
              <Col lg="6" xs="12" md="6">
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Name</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.motherboardName}
                    onChange={(e) => setNewTitle(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Vendor</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.motherboardVendor}
                    onChange={(e) => setNewVendor(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Chipset</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.motherboardChipset}
                    onChange={(e) => setNewChipset(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Memory Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewMemory(e.target.value);
                    }}
                  >
                    <option>{data.memory}</option>
                    <option value="ddr 2">DDR 2</option>
                    <option value="ddr 3">DDR 3</option>
                    <option value="ddr 4">DDR 4</option>
                    <option value="ddr 5">DDR 5</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Jumlah Slot Memori</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.memorySlot}
                    onChange={(e) => setNewMemorySlot(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Port Sata</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.sataPort}
                    onChange={(e) => setNewSataPort(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Slot M.2</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.m2Slot}
                    onChange={(e) => setNewM2Slot(e.target.value)}
                  />
                </Form.Group>
              </Col>
              <Col lg="6" xs="12" md="6">
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>USB Port</Form.Label>
                  <Form.Control
                    as="textarea"
                    defaultValue={data.usbPort}
                    onChange={(e) => setNewUsbPort(e.target.value)}
                    placeholder=""
                    style={{ height: "100px" }}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Display</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.display}
                    onChange={(e) => setNewDisplay(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Form Factor</Form.Label>
                  <br />
                  <Form.Check
                    inline
                    label="atx"
                    name="choseFormFactor"
                    type="radio"
                    defaultChecked={data.formFactor === "ATX" ? true : false}
                    id="atx"
                    required
                    onClick={(e) => setNewFormFactor(e.target.id)}
                  />
                  <Form.Check
                    inline
                    label="micro atx"
                    name="choseFormFactor"
                    defaultChecked={data.formFactor === "MATX" ? true : false}
                    type="radio"
                    id="matx"
                    onClick={(e) => setNewFormFactor(e.target.id)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Port Lan</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.lanPort}
                    onChange={(e) => setNewLanPort(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Socket</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.processorSocket}
                    onChange={(e) => setNewSocket(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.motherboardPrice}
                    onChange={(e) => setNewPrice(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Image</Form.Label>
                  <Form.Control type="file" onChange={loadImage} />
                </Form.Group>
              </Col>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="secondary"
              type="submit"
              className=" float-end ms-2"
            >
              Update
            </Button>
            <Button
              variant="danger"
              type="reset"
              className=" float-end"
              onClick={handleClose}
            >
              Cancel
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    );
  } else if (name === "user") {
    return (
      <Modal show={show} onHide={handleClose} backdrop="static">
        <Form onSubmit={updateProduct} onReset={resetForm}>
          <Modal.Header>
            <Modal.Title>Update User</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="card-body">
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Name</Form.Label>
                <Form.Control
                  type="text"
                  defaultValue={data.name}
                  onChange={(e) => setNewUsername(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="email"
                  defaultValue={data.email}
                  onChange={(e) => setNewEmail(e.target.value)}
                  required
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type="password"
                  onChange={(e) => setNewPassword(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Confirm Password</Form.Label>
                <Form.Control
                  type="password"
                  onChange={(e) => setNewConfPassword(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Role</Form.Label>
                <Form.Select
                  aria-label="select role"
                  onChange={(e) => setNewRole(e.target.value)}
                >
                  <option>{data.role}</option>
                  <option value="admin">Admin</option>
                  <option value="manager">Manager</option>
                  <option value="employe">Employe</option>
                </Form.Select>
              </Form.Group>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="secondary"
              type="submit"
              className=" float-end ms-2"
            >
              Update
            </Button>
            <Button
              variant="danger"
              className=" float-end"
              onClick={handleClose}
            >
              Cancel
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    );
  }

  return (
    <Modal show={show} onHide={handleClose} backdrop="static">
      <Form onSubmit={updateProduct} onReset={resetForm}>
        <Modal.Header>
          <Modal.Title>Update Processor</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="card-body">
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                defaultValue={
                  name === "processor" ? data.processorFullName : data.name
                }
                onChange={(e) => setNewTitle(e.target.value)}
              />
            </Form.Group>
            {name !== "processor" && name !== "memory" && name !== "storage" ? (
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Vendor</Form.Label>
                <Form.Control
                  type="text"
                  required
                  defaultValue={
                    data.vendor === undefined ? data.brand : data.vendor
                  }
                  onChange={(e) => setNewVendor(e.target.value)}
                />
              </Form.Group>
            ) : (
              ""
            )}
            {name === "processor" ? (
              <>
                {" "}
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Vendor</Form.Label>
                  <Form.Select
                    aria-label="select role"
                    onChange={(e) => {
                      setNewVendor(e.target.value);
                      getTypeProcessor(e.target.value);
                    }}
                  >
                    <option value={data.processorVendor}>
                      {data.processorVendor}
                    </option>
                    <option value="Intel">Intel</option>
                    <option value="AMD">AMD</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewType(e.target.value);
                    }}
                  >
                    <option value={data.processorType}>
                      {data.processorType}
                    </option>
                    {listType
                      ? listType.map((type, index) => {
                          return (
                            <option key={index} value={type.type}>
                              {type.type}
                            </option>
                          );
                        })
                      : () => {
                          return <option value="AMD">AMD</option>;
                        }}
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Socket</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.processorSocket}
                    onChange={(e) => setNewSocket(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Gen</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.processorGen}
                    onChange={(e) => setNewGen(e.target.value)}
                  />
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "memory" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewType(e.target.value);
                    }}
                  >
                    <option value={data.type} className="bg-success">
                      {data.type}
                    </option>
                    <option value="ddr 2">DDR 2</option>
                    <option value="ddr 3">DDR 3</option>
                    <option value="ddr 4">DDR 4</option>
                    <option value="ddr 5">DDR 5</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Channel</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewChannel(e.target.value);
                    }}
                  >
                    <option value={data.channel}>{data.channel}</option>
                    <option value="single channel">Single Channel</option>
                    <option value="dual channel">Dual Channel</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Speed</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.speed}
                    onChange={(e) => setNewSpeed(e.target.value)}
                  />
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "memory" ||
            name === "storage" ||
            name === "gamingDesk" ||
            name === "mousepad" ||
            name === "vga" ? (
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Size</Form.Label>
                {name === "memory" || name === "vga" ? (
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewSize(e.target.value);
                    }}
                  >
                    <option
                      value={
                        data.size === undefined ? data.memorySize : data.size
                      }
                    >
                      {data.size === undefined ? data.memorySize : data.size}
                    </option>
                    <option value="2 gb">2 Gb</option>
                    <option value="4 gb">4 GB</option>
                    <option value="6 gb">6 GB</option>
                    <option value="8 gb">8 GB</option>
                    <option value="16 gb">16 GB</option>
                    <option value="32 gb">32 GB</option>
                  </Form.Select>
                ) : (
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.size}
                    placeholder="120 GB"
                    onChange={(e) => setNewSize(e.target.value)}
                  />
                )}
              </Form.Group>
            ) : (
              ""
            )}

            {name === "storage" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewType(e.target.value);
                    }}
                  >
                    <option className="bg-success text-white">
                      {data.type}
                    </option>
                    <option value="hdd">Harddisk</option>
                    <option value="sdd">SSD</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Slot Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewSlotType(e.target.value);
                    }}
                  >
                    <option className="bg-success text-white">
                      {data.slotType}
                    </option>
                    <option value="sata">SATA</option>
                    <option value="msata">MSATA</option>
                    <option value="m.2 sata">M.2 SATA</option>
                    <option value="m.2 nvme">M.2 NVME</option>
                  </Form.Select>
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "cassing" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Form Factor</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewFormFactor(e.target.value);
                    }}
                  >
                    <option value={data.formFactor}>{data.formFactor}</option>
                    <option value="atx">ATX</option>
                    <option value="matx">MATX</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Font Panel</Form.Label>
                  <Form.Control
                    type="text"
                    defaultValue={data.frontPanel}
                    onChange={(e) => setNewFrontPanel(e.target.value)}
                  />
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "vga" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewType(e.target.value);
                    }}
                  >
                    <option value={data.memoryType}>{data.memoryType}</option>
                    <option value="gddr 4">GDDR 4</option>
                    <option value="gddr 5">GDDR 5</option>
                    <option value="gddr 6">GDDR 6</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Request PSU</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.reqPsu}
                    onChange={(e) => setNewReqPsu(e.target.value)}
                  />
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "psu" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Watt</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.watt}
                    onChange={(e) => setNewWatt(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Sertification</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.sertifikasi}
                    onChange={(e) => setNewSertification(e.target.value)}
                  />
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "monitor" || name === "mouse" ? (
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Resolusi</Form.Label>
                <Form.Control
                  type="text"
                  required
                  defaultValue={data.resolusi}
                  onChange={(e) => setNewResolusi(e.target.value)}
                />
              </Form.Group>
            ) : (
              ""
            )}

            {name === "monitor" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Panel</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    defaultValue={data.panel}
                    onChange={(e) => setNewPanel(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Port</Form.Label>
                  <Form.Control
                    type="text"
                    required
                    value={data.port}
                    onChange={(e) => setNewPort(e.target.value)}
                  />
                </Form.Group>
              </>
            ) : (
              ""
            )}
            {name === "mouse" ? (
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Button</Form.Label>
                <Form.Control
                  type="text"
                  required
                  defaultValue={data.button}
                  onChange={(e) => setNewButton(e.target.value)}
                />
              </Form.Group>
            ) : (
              ""
            )}

            {name === "keyboard" ||
            name === "mouse" ||
            name === "headset" ||
            name === "speaker" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Connection</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewConnection(e.target.value);
                    }}
                  >
                    <option
                      value={
                        name === "headset" ? data.portKoneksi : data.koneksi
                      }
                    >
                      {name === "headset" ? data.portKoneksi : data.koneksi}
                    </option>
                    <option value="wired">Wired</option>
                    <option value="wireless">Wireless</option>
                    <option value="bluetooth">Bluetooth</option>
                  </Form.Select>
                </Form.Group>
                {name === "keyboard" || name === "mouse" ? (
                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>RGB</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      onChange={(e) => {
                        setNewRgb(e.target.value);
                      }}
                    >
                      <option value={data.rgb}>{data.rgb}</option>
                      <option value="no">NO</option>
                      <option value="yes">YES</option>
                    </Form.Select>
                  </Form.Group>
                ) : (
                  ""
                )}
              </>
            ) : (
              ""
            )}

            {name === "keyboard" ? (
              <>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewType(e.target.value);
                    }}
                  >
                    <option value={data.type}>{data.type}</option>
                    <option value="standard">Standard</option>
                    <option value="mechanical">Mechanical</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Size</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewSize(e.target.value);
                    }}
                  >
                    <option value={data.size}>{data.size}</option>
                    <option value="Full-Size / 100%">Full-Size / 100%</option>
                    <option value="80%">80%</option>
                    <option value="75%">75%</option>
                    <option value="60%">60%</option>
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Switch Type</Form.Label>
                  <Form.Select
                    aria-label="Default select example"
                    onChange={(e) => {
                      setNewSwitchType(e.target.value);
                    }}
                  >
                    <option value={data.switchType}>{data.switchType}</option>
                    <option value="none">None</option>
                    <option value="blue switch">Blue Switch</option>
                    <option value="red switch">Red Switch</option>
                    <option value="brown switch">Brown Switch</option>
                  </Form.Select>
                </Form.Group>
              </>
            ) : (
              ""
            )}

            {name === "headset" ? (
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Cable Lenght</Form.Label>
                <Form.Control
                  type="text"
                  required
                  defaultValue={data.panjangKabel}
                  onChange={(e) => setNewCableLenght(e.target.value)}
                />
              </Form.Group>
            ) : (
              ""
            )}

            {name === "mousepad" ? (
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Ketebalan</Form.Label>
                <Form.Control
                  type="text"
                  required
                  defaultValue={data.ketebalan}
                  onChange={(e) => setNewKetebalan(e.target.value)}
                />
              </Form.Group>
            ) : (
              ""
            )}

            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Price</Form.Label>
              <Form.Control
                type="number"
                defaultValue={data.price}
                onChange={(e) => setNewPrice(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Image</Form.Label>
              <Form.Control type="file" onChange={loadImage} />
            </Form.Group>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" type="submit" className=" float-end ms-2">
            Update
          </Button>
          <Button variant="danger" className=" float-end" onClick={handleClose}>
            Cancel
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default ModalUpdate;
